/** @flow */
// import Immutable from 'immutable'
import { List } from 'immutable'
import React, { PureComponent } from 'react'
import { AutoSizer, Column, Table, SortDirection, SortIndicator } from 'react-virtualized'

//https://github.com/borisyankov/react-sparklines
//import { Sparklines, SparklinesLine, SparklinesSpots } from 'react-sparklines';
import { ForestPlots, ForestPlotsLine } from './ForestPlots/ForestPlots';

// import 'react-virtualized/styles.css'
// import styles from './Table.example.css'
import styles from './styles.css'

export default class ForestPlotTable extends PureComponent {
  constructor (props) {
    super(props)

    this.state = {
      disableHeader: false,
      headerHeight: 30,
      height: 450,
      hideIndexRow: false,
      overscanRowCount: 10,
      rowHeight: 45,
      rowCount: this.props.tableList.length,
      scrollToIndex: undefined,
      sortBy: 'trait_name', //'h2o',
      sortDirection: SortDirection.DESC, //SortDirection.ASC,
      useDynamicRowHeight: false
    }

    this._getRowHeight = this._getRowHeight.bind(this)
    this._headerRenderer = this._headerRenderer.bind(this)
    this._noRowsRenderer = this._noRowsRenderer.bind(this)
    this._onRowCountChange = this._onRowCountChange.bind(this)
    this._onScrollToRowChange = this._onScrollToRowChange.bind(this)
    this._rowClassName = this._rowClassName.bind(this)
    this._sort = this._sort.bind(this)

    this._linkRenderer = this._linkRenderer.bind(this)
    this._sparklineRenderer = this._sparklineRenderer.bind(this)
    
    this.updateTable = this.updateTable.bind(this)
  }
    
  updateTable() {
      this.setState({rowCount: this.props.tableList.length})
  }

  render () {
    const {
      disableHeader,
      headerHeight,
      height,
      hideIndexRow,
      overscanRowCount,
      rowHeight,
      rowCount,
      scrollToIndex,
      sortBy,
      sortDirection,
      useDynamicRowHeight
    } = this.state
    
    var list = List(this.props.tableList)
    
    const sortedList = this._isSortEnabled()
      ? list
        .sortBy(item => item[sortBy])
        .update(list =>
          sortDirection === SortDirection.ASC //DESC // Swap: sort descending by default
            ? list.reverse()
            : list
        )
      : list

    const rowGetter = ({ index }) => this._getDatum(sortedList, index)
    
//    if (this.props.tableList.length == 0) {
//        // console.log("No rows!");
//        return (
//            <div></div>
//        )
//    }
    
    return (
        <div>
          <AutoSizer disableHeight>
            {({ width }) => (
              <Table
                disableHeader={disableHeader}
                headerClassName={styles.headerColumn}
                headerHeight={headerHeight}
                height={height}
                noRowsRenderer={this._noRowsRenderer}
                overscanRowCount={overscanRowCount}
                // rowClassName={this._rowClassName}
                rowHeight={useDynamicRowHeight ? this._getRowHeight : rowHeight}
                rowGetter={rowGetter}
                rowCount={rowCount}
                scrollToIndex={scrollToIndex}
                sort={this._sort}
                sortBy={sortBy}
                sortDirection={sortDirection}
                width={width}
              >
                <Column
                  dataKey='trait_type'
                  disableSort={!this._isSortEnabled()}
                  label='Trait type'
                  headerRenderer={this._headerRenderer}
                  width={105}
                />
                <Column
                  dataKey='physio_system'
                  disableSort={!this._isSortEnabled()}
                  label='Physio System'
                  headerRenderer={this._headerRenderer}
                  width={170}
                  flexGrow={1}
                />
                <Column
                  dataKey='trait_name'
                  disableSort={!this._isSortEnabled()}
                  label='Trait name'
                  headerRenderer={this._headerRenderer}
                  width={270}
                  flexGrow={2}
                />
                <Column
                  dataKey='trait_code'
                  disableSort={!this._isSortEnabled()}
                  label='Code'
                  headerRenderer={this._headerRenderer}
                  cellRenderer={this._linkRenderer}
                  width={70}
                />
                <Column
                  dataKey='site'
                  disableSort={!this._isSortEnabled()}
                  label='Site'
                  headerRenderer={this._headerRenderer}
                  width={80}
                />
                <Column
                  dataKey='model'
                  disableSort={!this._isSortEnabled()}
                  label='Model'
                  headerRenderer={this._headerRenderer}
                  width={60}
                />
                <Column
                  dataKey='nfam'
                  disableSort={!this._isSortEnabled()}
                  label='Nfam'
                  headerRenderer={this._headerRenderer}
                  width={50}
                />
                <Column
                  dataKey='h2o'
                  disableSort={!this._isSortEnabled()}
                  label='h2o'
                  headerRenderer={this._headerRenderer}
                  cellRenderer={this._sparklineRenderer}
                  width={90}
                />
              </Table>
            )}
          </AutoSizer>
        </div>
    )
  }

  _getDatum (list, index) {
    return list.get(index % list.size)
  }

  _getRowHeight ({ index }) {
    var list = List(this.props.tableList)
    console.log('this._getDatum(list, index).size',this._getDatum(list, index))

    return this._getDatum(list, index).size
  }

  _headerRenderer ({
    columnData,
    dataKey,
    disableSort,
    label,
    sortBy,
    sortDirection
  }) {
    // console.log("in _headerRenderer: sortDirection",sortDirection)
    var displaySortDirection = sortDirection === 'ASC'? 'DESC' : 'ASC';
      
    return (
      <div>
        {label}
        {sortBy === dataKey &&
          <SortIndicator sortDirection={displaySortDirection} />
        }
      </div>
    )
  }

  _linkRenderer ({ cellData, columnData, dataKey, rowData, rowIndex }) {
    // console.log(cellData.startsWith('PMID'));
    var linkType = "default";
    if (cellData.startsWith('PMID')) {
        linkType = "pubmed"
    }
    
    switch (linkType) {
      case "pubmed":
        var pubmedID = cellData.split(' ')[1];
        var outLink = "https://www.ncbi.nlm.nih.gov/pubmed/"+pubmedID
        return (
        <a target="_blank" href={outLink}>{'PMID '+pubmedID}</a>
        )
        break;
//      case value2:
//        
//        break;
      default:
        return ( <div>{cellData}</div> )
        break;
    }
          
  }

  _sparklineRenderer ({ cellData, columnData, dataKey, rowData, rowIndex }) {
    var miniText = {
            fontSize: '12px', display: 'flex', justifyContent: 'center'
        };
    var emphasizedText = { fontWeight: 500 }; //<span style={emphasizedText}>
            
    if (cellData == "") {
        return <div></div>
    }
    
    return (
     <div className="sparkline-cell">
        <div className="h2o-est" style={miniText}>
            <b>{cellData[0]}</b>&nbsp;{'['+cellData[1]+', '+cellData[2]+']'}
        </div>
        <ForestPlots data={[cellData[1], cellData[0], cellData[2]]}>
            <ForestPlotsLine />
        </ForestPlots>
    </div>
    )
  }

  _isSortEnabled () {
    //const { list } = this.context
    // const list = List(this.props.tableList)
    // const { rowCount } = this.state
    //console.log("_isSortEnabled",rowCount <= list.size)

    return true //rowCount <= list.size
  }

  _noRowsRenderer () {
    return (
      <div className={styles.noRows}>
        No rows
      </div>
    )
  }

  _onRowCountChange (event) {
    const rowCount = parseInt(event.target.value, 10) || 0

    this.setState({ rowCount })
  }

  _onScrollToRowChange (event) {
    const { rowCount } = this.state
    let scrollToIndex = Math.min(rowCount - 1, parseInt(event.target.value, 10))

    if (isNaN(scrollToIndex)) {
      scrollToIndex = undefined
    }

    this.setState({ scrollToIndex })
  }

  _rowClassName ({ index }) {
    if (index < 0) {
      return styles.headerRow
    } else {
      return index % 2 === 0 ? styles.evenRow : styles.oddRow
    }
  }

  _sort ({ sortBy, sortDirection }) {
    this.setState({ sortBy, sortDirection})
  }

  _updateUseDynamicRowHeight (value) {
    this.setState({
      useDynamicRowHeight: value
    })
  }
}



