export default ({ data, limit, width = 1, height = 1, margin = 0,   max = 1, min = 0 }) => {

    const len = data.length;

    if (limit && limit < len) {
        data = data.slice(len - limit);
    }

    const vfactor = (height - margin * 2);
    const hfactor = (width - margin * 2) / ((max - min) || 2);

    return data.map((d, i) => ({
        x: (max === min ? 1 : (d - min)) * hfactor + margin,
        y: vfactor/2
    }));
};



