import React from 'react';
//import Select from 'react-select'
import Select from './CustomSelect/Select'
import ForestPlotTable from './ForestPlotTable.js'
import traitData from './heritability-traits'
import traits from './trait-select-options'

//import 'react-select/dist/react-select.css'
import './CustomSelect/react-select.css'

var emptyTable = [{"trait_type": "",
                   "physio_system": "",
                   "trait_name": "",
                   "trait_code": "",
                   "site": "",
                   "model": "",
                   "nfam": "",
                   "h2o": "" }];

export default class App extends React.Component {
    
    constructor(props){
        super(props);
        
        this.state = {
			options: traits,
			value: [],
            tableList: traitData,
            
            useTraitType: {'icd_9': true, 'icd_10': true, 'lab': true,
                           'lit': true, 'semi': true}
        }
    
    this.handleTypeChange = this.handleTypeChange.bind(this);
    this.handleSelectChange = this.handleSelectChange.bind(this);
    }
    
    handleTypeChange (traitType) {
        const changedTypes = this.state.useTraitType;
        changedTypes[traitType] = !this.state.useTraitType[traitType];
        
        // console.log(changedTypes, this.state.value, this.state.tableList.length);
        
        this.setState({useTraitType: changedTypes}, () => {
            // console.log(this.state.value, this.state.useTraitType);
            
            var splitValues = this.state.value == '' ? '': this.state.value.split(/[,|]/);
            var filteredData = traitData.filter( function (entry) {
                if (this.state.value != '' && splitValues.indexOf(entry.trait_code) == -1) {
                    return false; // trait not selected
                }
                
                var traitIncluded = false; // Check if trait_type in checked boxes
                for (var key in this.state.useTraitType) {
                    if (this.state.useTraitType[key]) {
                        var entryTraitType = entry.trait_type.toLowerCase();
                        if ( entryTraitType.startsWith(key.replace('_','-')) ) {
                            traitIncluded = true;
                        }
                    }
                }
                
                return traitIncluded;
            }, this);
            
            if (filteredData.length == 0) { // Prevents empty table error
                // filteredData = traitData;
                filteredData = emptyTable;
            }
            
            this.setState({tableList: filteredData}, () => {
                // console.log("this.state.tableList", this.state.tableList.length);
                this.refs.traitTable.updateTable();
            })
        });
    }
    
    handleSelectChange (value) {        
		this.setState({ value
        }, () => {
            var splitValues = this.state.value.split(/[,|]/);
            // console.log(splitValues);
            
            var filteredData = traitData.filter( function (entry) {
                // Confirm that entry.trait_type is checked                
                var traitIncluded = false;
                for (var key in this.state.useTraitType) {
                    if (this.state.useTraitType[key]) {
                        var entryTraitType = entry.trait_type.toLowerCase();
                        if ( entryTraitType.startsWith(key.replace('_','-')) ) {
                            traitIncluded = true;
                        }
                    }
                }
                
                if (this.state.value == '') {
                    return traitIncluded;
                } else {
                    return (splitValues.indexOf(entry.trait_code) != -1) && traitIncluded;
                }
            }, this);
            
            if (filteredData.length == 0) { // Prevents empty table error
                // filteredData = traitData;
                filteredData = emptyTable;
            }
            
            this.setState({tableList: filteredData}, () => {
                // console.log("this.state.tableList", this.state.tableList.length);
                this.refs.traitTable.updateTable();
            })
        } );              
	}
    
    render(){        
        var numResultsStyle = {float:"right"};
        var numResults = this.state.tableList.length == 1? "1 result": this.state.tableList.length + " results";
        if (this.state.tableList == emptyTable) {
            numResults = "No results found";
        }
        
        return (
            <div id="riftehr-app">
            <h2 className="riftehr-app-title" style={{textAlign:"center"}}>RIFTEHR: high confidence observational heritability estimates</h2>
                <div id="select-bar" style={{paddingTop:"10px"}}>
                    <Select multi
                      simpleValue
                      name="selected-traits"
                      value={this.state.value}
                      placeholder="Select one or more traits..."
                      noResultsText="Trait not found" 
                      options={this.state.options}
                      onChange={this.handleSelectChange}
                      tabSelectsValue={false}
                    />
                
                    <div className="checkbox-list" style={{paddingTop:"5px"}}>
    					<label className="checkbox-inline">
    						<input type="checkbox" className="checkbox-control" onChange={this.handleTypeChange.bind(this,'icd_9')} checked={this.state.useTraitType.icd_9} />
    						<span className="checkbox-label">ICD-9</span>
    					</label>
                        &nbsp;&nbsp;
    					<label className="checkbox-inline">
    						<input type="checkbox" className="checkbox-control" onChange={this.handleTypeChange.bind(this,'icd_10')} checked={this.state.useTraitType.icd_10} />
    						<span className="checkbox-label">ICD-10</span>
    					</label>
                        &nbsp;&nbsp;
    					<label className="checkbox-inline">
    						<input type="checkbox" className="checkbox-control" onChange={this.handleTypeChange.bind(this,'lab')} checked={this.state.useTraitType.lab} />
    						<span className="checkbox-label">Lab</span>
    					</label>
                        &nbsp;&nbsp;
    					<label className="checkbox-inline">
    						<input type="checkbox" className="checkbox-control" onChange={this.handleTypeChange.bind(this,'lit')} checked={this.state.useTraitType.lit} />
    						<span className="checkbox-label">Literature</span>
    					</label>
                        &nbsp;&nbsp;
    					<label className="checkbox-inline">
    						<input type="checkbox" className="checkbox-control" onChange={this.handleTypeChange.bind(this,'semi')} checked={this.state.useTraitType.semi} />
    						<span className="checkbox-label">Manually Curated</span>
    					</label>
                        
                        <div style={numResultsStyle}>{numResults}</div>
    				</div>
                </div>
            
                <br/>
                <ForestPlotTable ref="traitTable" tableList={this.state.tableList} />
            </div>
         )
    }
}


