import React from 'react';

export default class ForestPlotsLine extends React.Component {

    static propTypes = {
        color: React.PropTypes.string,
        style: React.PropTypes.object,
        spotSize: React.PropTypes.number,
    };

    static defaultProps = {
        style: {},
        spotSize: 12
    };

    render() {
        const { points, width, height, margin, color, style, spotSize } = this.props;
        // console.log("points",points)

        const linePoints = points
            .map((p) => [p.x, p.y])
            .reduce((a, b) => a.concat(b));

        const lineStyle = {
            stroke: color || style.stroke || 'black',
            strokeWidth: style.strokeWidth || '7',
            strokeLinejoin: style.strokeLinejoin || 'round',
            strokeLinecap: style.strokeLinecap || 'round',
            fill: 'none'
        };

        return (
            <g>
                <polyline points={linePoints.join(' ')} style={lineStyle} />
                <circle
                            cx={points[1].x}
                            cy={points[1].y}
                            r={spotSize}
                            style={style} />
            </g>
        )
    }
}



